.breadcrumbs {
    background: #F3F3F3;
    padding-top: 20px;
    padding-bottom: 20px;
    width: 100%;
    @media (min-width: 1024px) {
        padding-top: 20px;
        padding-bottom: 30px;
    }
    p {
        color: var(--Grey);
        font-family: Circe;
        font-size: 12px;
        font-style: normal;
        font-weight: 350;
        line-height: normal;
        letter-spacing: 0.6px;
        text-transform: uppercase;
        margin: unset;
        a {
            color: inherit;
            transition: .4s;
            &:hover {
                color: black;
            }
        }
    }
    h1 {
        margin: unset;
        margin-top: 20px;
        color: #000;
        font-family: Circe;
        font-size: 20px;
        font-style: normal;
        font-weight: 400;
        line-height: normal;
        letter-spacing: 3px;
        text-transform: uppercase;
        @media (min-width: 1024px) {
            margin-top: 43px;
        }
    }
}

.breadcrumbs-mini {
    width: 100%;
    border-top: 1px solid var(--plashka);
    padding-top: 20px;

    p + a {
        display: flex;
        column-gap: 6px;
        color: var(--Grey, #808080);
        font-size: 12px;
        font-style: normal;
        font-weight: 400;
        line-height: 14px;
        letter-spacing: 0.6px;
        text-transform: uppercase;
    }
    @media (max-width: 767px) {
        p {
            display: none;
        }
    }
    @media (min-width: 768px) {
        p + a {
            display: none;
        }
        p {
            color: var(--Grey);
            font-size: 12px;
            font-style: normal;
            font-weight: 400;
            line-height: normal;
            letter-spacing: 0.6px;
            text-transform: uppercase;
            a {
                color: inherit;
                transition: .4s;
                &:hover {
                    color: var(--Dark-grey);
                }
            }
        }
    }

}